.toggle-menu-btn {
   position: absolute;
   top: 15px;
   left: 20px;
   font-size: 2.5rem;
   color: #fff;
}
.menu {
   position: fixed;
   bottom: 0;
   top: 0;
   left: 0;
   min-width: 30vw;
   max-width: 70vw;
   background-color: #f1f1f1;
   color: #333;
   transform: translateX(-60%);
   padding: 15px 20px;
   animation: transform 0.2s linear;
   animation: opacity 0.2s linear;
   opacity: 0;
   pointer-events: none;
}
.menu.open {
   transform: translateX(0);
   pointer-events: unset;
   opacity: 1;
}
.menu-header {
   text-align: right;
}
.close-menu-btn {
   margin-left: auto;
   font-size: 2.5rem;
}

.menu-form {
   overflow: auto;
   display: flex;
   flex-direction: column;
   gap: 10px;
}
.form-group {
   display: flex;
   padding: 8px 0;
   flex-direction: column;
   gap: 8px;
   border-top: 1px solid #ccc;
}
.form-group label {
   font-size: 2.5rem;
   color: #333;
}
.form-group input {
   padding: 8px 0px;
   width: 100%;
   font-size: 2.5rem;
}
.form-group:first-child {
   border-top: none;
}

.menu-submit-btn {
   font-size: 2.5rem;
   background-color: #333;
   border-radius: 6px;
   color: #fff;
   padding: 6px 0;
}
